html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  color: var(--main-light-text);

  font-family: var(--primary-font-family);
  font-size: 18px;
  line-height: 1.555;
  letter-spacing: 0.08em;
}

h1,
h2,
h3,
h4,
h5,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
